/**
Footer.scss
Create a Page for a footer, then assign section CSS class `footer-section`
**/
.footer-section{
    --sectionBgColor: var(--colorT1);
    --linkSize: 14px;
    --defaultSpace: 60px;
    --linkColor: var(--colorP1);
    --linkDecoration: none;
    --linkWeight: var(--regular);
    --linkColorHover: var(--colorP1);
    --linkTransition: opacity ease-in-out 0.2s;
}

.footer-page-content,
.footer-page-content .footer-section{
    border-top: 0;
}
.footer-section{
    .container{
        padding-top: 0 !important;
    }
    h6, .h6 .av-special-heading-tag{
        --headingColor: var(--colorP1);
        --headingLetterSpacing: calc( ( var(--headingSize) / 1000 ) * 25 );
    }
    a{
        text-decoration: var(--linkDecoration) !important;
        &:hover:not(:active):not(.no-link){
            opacity: 0.85;
        }
    }
    .ep-columns{
        --epColumnSpace: calc( var(--footerGap, 15px) * 2 );
        &:not(.footer--menu-row){
            @media only screen and (max-width: 989px){
                text-align: center;
            }
        }
        &:last-child:not(.footer--menu-row){
            @media only screen and (max-width: 767px){
                --topSpace: 50px;
            }
        }
    }
    .avia_codeblock{
        display: block;
        line-height: 1;
    }
    .logo{
        opacity: 1 !important;
        display: inline-block;
        width: var(--footerLogoWidth, 116px);
        height: var(--footerLogoHeight, 36px);
        img{
            display: block;
        }
    }
    .ep-social-bookmarks-wrapper{
        --linkColor: var(--colorP1);
        --linkSize: 30px;
        ul{
            display: flex;
            flex-wrap: wrap;
            flex-direction: unset;
            margin: -5px -12.5px;
            @media only screen and (min-width: 990px){
                justify-content: flex-end;
            }
            @media only screen and (max-width: 989px){
                justify-content: center;
            }
        }
        li{
            margin: 5px 12.5px;
        }
        a{
            display: inline-block;
            line-height: 1;
        }
    }
    .footer--menu-row{
        @media only screen and (max-width: 989px){
            margin-top: 0;
            margin-bottom: 0;
        }
        .ep-column{
            margin-bottom: 0;
        }
        &:not(.ep-break-at-tablet){
            .ep-column{
                @media only screen and (max-width: 989px){
                    --epColumnWidth: 50%;
                }
                @media only screen and (max-width: 480px){
                    --epColumnWidth: 100%;
                }
            }
        }
        .ep-custom-menu-style-vertical{
            .menu{
                flex-direction: column;
            }    
        }
        .ep-custom-menu-style-horizontal{
            .menu{
                flex-direction: row;
            }    
        }
    }
    .footer--socket-row{
        --linkSize: inherit;
        font-size: 14px;
        .ep-column:not(:last-child){
            @media only screen and (max-width: 767px){
                margin-bottom: 10px;
            }
        }
    }
    .footer--menu-heading{
        margin-top: 0;
        margin-bottom: 0;
        padding-bottom: 10px;
        border-bottom: var(--footerHeadingMenuBorder, 1px solid var(--colorE6));
        @media only screen and (max-width: 989px){
            margin-top: var(--topSpace, 35px);
        }
    }
    .menu{
        font-family: var(--bodyFontFamily);
        display: flex;
        flex-wrap: wrap;
        margin-left: calc( var(--footerGap, 15px) * -1 );
        margin-right: calc( var(--footerGap, 15px) * -1 );
        & > .menu-item{
            flex: 1;
            padding-left: var(--footerGap, 15px);
            padding-right: var(--footerGap, 15px);
            @media only screen and (max-width: 480px){
                flex: unset;
                width: 100%;
            }
        }
        .menu-item-inner{
            color: var(--footerMenuItemColor, var(--colorP1));
        }
        .menu-title{
            margin-top: 25px;
            & > .menu-item-inner{
                font-size: var(--footerMenuTitleSize, 16px);
                color: var(--footerMenuTitleColor, var(--colorP2));
                font-weight: var(--bold);
            }
        }
        .sub-menu{
            display: flex;
            flex-direction: column;
            .menu-item{
                margin-top: var(--footerMenuItemGap, 10px);
            }
        }
    }
}